const EmptyIcon = styled.div`
  font-size: 2.5rem;
  margin-bottom: 1rem;

  @media (max-width: 768px) {
    font-size: 2rem;
  }
`;

const EmptyText = styled.p`
  font-size: 1rem;

  @media (max-width: 768px) {
    font-size: 0.9rem;
  }
`;

const TargetList = ({ group, targets, onClose, onTargetUpdate }) => {
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedTarget, setSelectedTarget] = useState(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false);
  const [targetToDelete, setTargetToDelete] = useState(null);

  //   
  const formatPhoneNumber = (phone) => {
    if (!phone) return '';
    
    //  
    const numbers = phone.replace(/\D/g, '');
    
    //   (010, 011, 016, 017, 018, 019) - 11
    if (numbers.length === 11 && (numbers.startsWith('010') || numbers.startsWith('011') || 
                                 numbers.startsWith('016') || numbers.startsWith('017') || 
                                 numbers.startsWith('018') || numbers.startsWith('019'))) {
      return `${numbers.slice(0, 3)}-${numbers.slice(3, 7)}-${numbers.slice(7)}`;
    }
    
    //   (02) - 9  10
    if (numbers.startsWith('02')) {
      if (numbers.length === 9) {
        return `${numbers.slice(0, 2)}-${numbers.slice(2, 5)}-${numbers.slice(5)}`;
      } else if (numbers.length === 10) {
        return `${numbers.slice(0, 2)}-${numbers.slice(2, 6)}-${numbers.slice(6)}`;
      }
    }
    
    //   (031, 032, 033, 041, 042, 043, 051, 052, 053, 061, 062, 063) - 10
    if (numbers.length === 10 && (numbers.startsWith('031') || numbers.startsWith('032') || numbers.startsWith('033') ||
                                 numbers.startsWith('041') || numbers.startsWith('042') || numbers.startsWith('043') ||
                                 numbers.startsWith('051') || numbers.startsWith('052') || numbers.startsWith('053') ||
                                 numbers.startsWith('061') || numbers.startsWith('062') || numbers.startsWith('063'))) {
      return `${numbers.slice(0, 3)}-${numbers.slice(3, 6)}-${numbers.slice(6)}`;
    }
    
    //  10 
    if (numbers.length === 10) {
      return `${numbers.slice(0, 3)}-${numbers.slice(3, 6)}-${numbers.slice(6)}`;
    }
    
    // 11  
    if (numbers.length >= 11) {
      return `${numbers.slice(0, 3)}-${numbers.slice(3, 7)}-${numbers.slice(7, 11)}`;
    }
    
    return phone; //      
  };

  const filteredTargets = targets.filter(target =>
    (target.name && target.name.toLowerCase().includes(searchTerm.toLowerCase())) ||
    (target.mobilePhone && target.mobilePhone.includes(searchTerm)) ||
    (target.phone && target.phone.includes(searchTerm)) ||
    (target.targetType && target.targetType.includes(searchTerm)) ||
    (target.targetHousehold && target.targetHousehold.includes(searchTerm))
  );

  const handleEdit = (target) => {
    setSelectedTarget(target);
    setIsModalOpen(true);
  };

  const handleDelete = (target) => {
    setTargetToDelete(target);
    setIsDeleteModalOpen(true);
  };

  const handleAdd = () => {
    setSelectedTarget(null);
    setIsModalOpen(true);
  };

  const handleSave = async (targetData) => {
    try {
      if (selectedTarget) {
        // 
        await targetAPI.updateTarget(group.group_id, selectedTarget.id, targetData);
        
        //  
        const response = await targetAPI.getTargets(group.group_id);
        if (response.ok && response.data) {
          onTargetUpdate(response.data);
        }
      } else {
        // 
        const response = await targetAPI.createTarget(group.group_id, targetData);
        if (response.ok) {
          //  
          const targetsResponse = await targetAPI.getTargets(group.group_id);
          if (targetsResponse.ok && targetsResponse.data) {
            onTargetUpdate(targetsResponse.data);
          }
        }
      }
      
      setIsModalOpen(false);
      setSelectedTarget(null);
    } catch (error) {
      console.error('  :', error);
      alert(' .  .');
    }
  };

  const handleConfirmDelete = async () => {
    try {
      await targetAPI.deleteTarget(group.group_id, targetToDelete.id);
      
      //  
      const response = await targetAPI.getTargets(group.group_id);
      if (response.ok && response.data) {
        onTargetUpdate(response.data);
      }
      
      setIsDeleteModalOpen(false);
      setTargetToDelete(null);
    } catch (error) {
      console.error('  :', error);
      alert(' .  .');
    }
  };

  const handleExcelDownload = () => {
    //   
    const wb = XLSX.utils.book_new();
    
    //   
    const wsData = [];
    
    // 1.  
    wsData.push(['1.']);
    wsData.push(['', '']);
    wsData.push(['', '']);
    wsData.push(['', '']);
    wsData.push(['', '']);
    wsData.push(['', '']);
    wsData.push([]);
    
    // 2.   
    wsData.push(['2.']);
    wsData.push(['', '', '', '', '', '', '', '', '', '', '']);
    
    // wrapText   -   

    //   
    targets.forEach((target, index) => {
      const addressParts = target.address ? target.address.split(' ') : ['', ''];
      const zipcode = addressParts[0] || '';
      const roadAddress = addressParts.slice(1).join(' ') || '';
      
      wsData.push([
        index + 1,
        target.name || '',
        target.targetType || '',
        target.targetHousehold || '',
        zipcode,
        roadAddress,
        target.detailAddress || '',
        target.mobilePhone || '',
        target.phone || '',
        target.applicationReason || '',
        target.directions || ''
      ]);
    });
    
    //    
    if (group) {
      //     
      wsData[1][1] = group.group_name || '';  // 
      wsData[2][1] = `${group.zipcode || ''} ${group.address1 || ''} ${group.address2 || ''}`.trim();  // 
      wsData[3][1] = group.contact_name || '';      // 
      wsData[4][1] = formatPhoneNumber(group.mobile_phone || '');      // 
      wsData[5][1] = formatPhoneNumber(group.office_phone || '');      // 
    }
    
    //  
    const ws = XLSX.utils.aoa_to_sheet(wsData);
    
    //    -      
    const range = XLSX.utils.decode_range(ws['!ref']);
    
    //     
    for (let row = range.s.r; row <= range.e.r; row++) {
      for (let col = range.s.c; col <= range.e.c; col++) {
        const cellAddress = XLSX.utils.encode_cell({ r: row, c: col });
        if (!ws[cellAddress]) continue;
        
        //   
        ws[cellAddress].s = {
          alignment: {
            vertical: 'top',  //  
            horizontal: 'left',
            wrapText: true
          },
          border: {
            top: { style: 'thin' },
            bottom: { style: 'thin' },
            left: { style: 'thin' },
            right: { style: 'thin' }
          }
        };
      }
    }
    
    //       (  )
    const applicationReasonCol = 9; //   (J)
    const directionsCol = 10; //   (K)
    
    for (let row = 9; row <= range.e.r; row++) { //    (9)
      const applicationReasonCell = XLSX.utils.encode_cell({ r: row, c: applicationReasonCol });
      const directionsCell = XLSX.utils.encode_cell({ r: row, c: directionsCol });
      
      if (ws[applicationReasonCell]) {
        ws[applicationReasonCell].s = {
          alignment: {
            vertical: 'top',  //   
            horizontal: 'left',
            wrapText: true,
            indent: 0
          },
          border: {
            top: { style: 'thin' },
            bottom: { style: 'thin' },
            left: { style: 'thin' },
            right: { style: 'thin' }
          }
        };
      }
      
      if (ws[directionsCell]) {
        ws[directionsCell].s = {
          alignment: {
            vertical: 'top',  //   
            horizontal: 'left',
            wrapText: true,
            indent: 0
          },
          border: {
            top: { style: 'thin' },
            bottom: { style: 'thin' },
            left: { style: 'thin' },
            right: { style: 'thin' }
          }
        };
      }
    }
    
    //    (   )
    const merges = [];
    
    ws['!merges'] = merges;
    
    //   
    ws['!cols'] = [
      { wch: 8 },  // 
      { wch: 15 }, // 
      { wch: 15 }, // 
      { wch: 20 }, // 
      { wch: 12 }, // 
      { wch: 25 }, // 
      { wch: 20 }, // 
      { wch: 15 }, // 
      { wch: 15 }, // 
      { wch: 50 }, //  ()
      { wch: 30 }  // 
    ];
    
    //    -     
    ws['!rows'] = [];
    for (let row = range.s.r; row <= range.e.r; row++) {
      let rowHeight = 20; //  
      
      // 1   (1-7):   
      // 2   (8):     
      // 2   (9):     
      if (row >= 9) { //   
        const applicationReasonCell = XLSX.utils.encode_cell({ r: row, c: 9 }); //  
        const directionsCell = XLSX.utils.encode_cell({ r: row, c: 10 }); //  
        
        let maxLines = 1; //  1
        
        //    
        if (ws[applicationReasonCell] && ws[applicationReasonCell].v) {
          const text = ws[applicationReasonCell].v.toString();
          const lines = text.split('\n').length;
          const wrappedLines = Math.ceil(text.length / 50); // 50 1 
          maxLines = Math.max(maxLines, Math.max(lines, wrappedLines));
        }
        
        //    
        if (ws[directionsCell] && ws[directionsCell].v) {
          const text = ws[directionsCell].v.toString();
          const lines = text.split('\n').length;
          const wrappedLines = Math.ceil(text.length / 30); // 30 1 
          maxLines = Math.max(maxLines, Math.max(lines, wrappedLines));
        }
        
        //      ( 20,  200)
        rowHeight = Math.min(200, Math.max(20, maxLines * 15 + 10));
      }
      
      ws['!rows'][row] = { hpt: rowHeight };
    }
    
    //   
    XLSX.utils.book_append_sheet(wb, ws, '');
    
    //   
    const fileName = `${group?.name || ''}__${new Date().toISOString().split('T')[0]}.xlsx`;
    XLSX.writeFile(wb, fileName);
  };

  return (
    <TargetListContainer>
      <Header>
        <HeaderTop>
          <Title> </Title>
          <CloseButton onClick={onClose}></CloseButton>
        </HeaderTop>
        <GroupInfo>
          {group.name} -  {targets.length}
        </GroupInfo>
      </Header>

      <ActionBar>
        <SearchInput
          type="text"
          placeholder=", , ,  ..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
        />
        <ButtonGroup>
          <ExcelButton onClick={handleExcelDownload}>
             
          </ExcelButton>
          <AddButton onClick={handleAdd}>
            +  
          </AddButton>
        </ButtonGroup>
      </ActionBar>

      <ListContainer>
        {filteredTargets.length === 0 ? (
          <EmptyState>
            <EmptyIcon></EmptyIcon>
            <EmptyText>
              {searchTerm ? '  .' : '  .'}
            </EmptyText>
          </EmptyState>
        ) : (
          filteredTargets.map((target) => (
            <TargetItem key={target.id}>
              <TargetHeader>
                <TargetName>{target.name}</TargetName>
                <TargetStatus status={target.status}>
                  {target.status === 'active' ? '' : ''}
                </TargetStatus>
              </TargetHeader>
              
              <TargetDetails>
                {target.targetType && <div> : {target.targetType}</div>}
                {target.targetHousehold && <div> : {target.targetHousehold}</div>}
                {target.mobilePhone && <div> : {formatPhoneNumber(target.mobilePhone)}</div>}
                {target.phone && <div> : {formatPhoneNumber(target.phone)}</div>}
                <div> {target.address}</div>
                {target.detailAddress && <div> : {target.detailAddress}</div>}
                {target.applicationReason && <div> : {target.applicationReason.substring(0, 50)}{target.applicationReason.length > 50 ? '...' : ''}</div>}
                {target.directions && <div> : {target.directions}</div>}
                <div> : {target.registeredAt}</div>
              </TargetDetails>
              
              <TargetActions>
                <ActionButton 
                  className="edit"
                  onClick={() => handleEdit(target)}
                >
                  
                </ActionButton>
                <ActionButton 
                  className="delete"
                  onClick={() => handleDelete(target)}
                >
                  
                </ActionButton>
              </TargetActions>
            </TargetItem>
          ))
        )}
      </ListContainer>

      {isModalOpen && (
        <TargetModal
          target={selectedTarget}
          onSave={handleSave}
          onClose={() => {
            setIsModalOpen(false);
            setSelectedTarget(null);
          }}
        />
      )}

      {isDeleteModalOpen && (
        <ConfirmModal
          title=" "
          message={`${targetToDelete?.name} ?`}
          onConfirm={handleConfirmDelete}
          onCancel={() => {
            setIsDeleteModalOpen(false);
            setTargetToDelete(null);
          }}
        />
      )}
    </TargetListContainer>
  );
};

export default TargetList;
