import axios from 'axios';

// API   -    
const API_BASE_URL = 'https://h1v071xnrh.execute-api.ap-northeast-2.amazonaws.com/prod';

const api = axios.create({
  baseURL: API_BASE_URL,
  timeout: 10000,
  headers: {
    'Content-Type': 'application/json',
  },
  // CORS   
  withCredentials: false,
  mode: 'cors',
});

//   -  
api.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('authToken');
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

//   -  
api.interceptors.response.use(
  (response) => {
    return response;
  },
  (error) => {
    //  API   
    if (error.response?.status === 401 && !error.config?.url?.includes('/login')) {
      localStorage.removeItem('authToken');
      localStorage.removeItem('user');
      window.location.href = '/';
    }
    return Promise.reject(error);
  }
);

//   API
export const authAPI = {
  // 
  login: async (businessNumber, password) => {
    try {
      console.log('API Base URL:', process.env.REACT_APP_API_BASE_URL);
      console.log('Full API URL:', api.defaults.baseURL);
      console.log('Login request to:', `${api.defaults.baseURL}/login`);
      
      const response = await api.post('/login', {
        businessNumber,
        password,
      });
      
      if (response.data.token) {
        localStorage.setItem('authToken', response.data.token);
        localStorage.setItem('user', JSON.stringify(response.data.user));
      }
      
      return response.data;
    } catch (error) {
      //     LoginPage    
      return {
        ok: false,
        message: error.response?.data?.message || ' .'
      };
    }
  },

  // 
  logout: async () => {
    try {
      await api.post('/auth/logout');
    } catch (error) {
      console.error('Logout error:', error);
    } finally {
      localStorage.removeItem('authToken');
      localStorage.removeItem('user');
    }
  },

  //  
  verifyToken: async () => {
    try {
      const response = await api.get('/auth/verify');
      return response.data;
    } catch (error) {
      throw new Error('  .');
    }
  },
};

//   API
export const businessAPI = {
  //   
  getBusinesses: async () => {
    try {
      const response = await api.get('/businesses');
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || '   .');
    }
  },

  //   
  getBusiness: async (businessId) => {
    try {
      const response = await api.get(`/businesses/${businessId}`);
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || '   .');
    }
  },

  //  
  createBusiness: async (businessData) => {
    try {
      const response = await api.post('/businesses', businessData);
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || '  .');
    }
  },

  //  
  updateBusiness: async (businessId, businessData) => {
    try {
      const response = await api.put(`/businesses/${businessId}`, businessData);
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || '  .');
    }
  },

  //  
  deleteBusiness: async (businessId) => {
    try {
      const response = await api.delete(`/businesses/${businessId}`);
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || '  .');
    }
  },
};

